import React, { useContext } from "react";

import { WeatherContext } from "../providers/WeatherContext";

export default function OutputCard() {
  const [weather] = useContext(WeatherContext);
  let d = new Date();
  let date = d.toDateString();
  let iconSrc = `http://openweathermap.org/img/wn/${weather.icon}@2x.png`;
  let windDirection = "";

  if (weather.deg === 0) {
    windDirection = "N";
  } else if (weather.deg === 90) {
    windDirection = "E";
  } else if (weather.deg === 180) {
    windDirection = "S";
  } else if (weather.deg === 270) {
    windDirection = "W";
  } else if (weather.deg > 1 && weather.deg < 90) {
    windDirection = "NE";
  } else if (weather.deg > 90 && weather.deg < 180) {
    windDirection = "SE";
  } else if (weather.deg > 180 && weather.deg < 270) {
    windDirection = "SW";
  } else if (weather.deg > 270 && weather.deg < 360) {
    windDirection = "SW";
  }

  return (
    <div className="card">
      <div className="card-header">
        <h2>
          {weather.name} - {weather.country}
        </h2>

        <h3>{weather.temp} &deg; C</h3>
      </div>
      <div className="card-columns">
        <div className="clouds">
          <div>
            <h2>{weather.clouds}</h2>
          </div>

          <br />
          <div>
            <h2>Clouds Count</h2>
          </div>
        </div>
        <hr className="first"></hr>
        <div className="weather">
          <div>
            <img src={iconSrc} alt="weather icon" />
          </div>
          <br />
          <div>
            <h2>{weather.weather}</h2>
          </div>
        </div>
        <hr></hr>
        <div className="wind">
          <div>
            <h2>
              {windDirection} - {weather.wind} kph
            </h2>
          </div>

          <br />
          <div>
            <h2>Wind</h2>
          </div>
        </div>
      </div>
      <h2 className="card-footer"> {date} </h2>
    </div>
  );
}
