import React, { useState, useContext, useEffect } from "react";
import { WeatherContext } from "../providers/WeatherContext";
import searchIcon from "../images/Search.svg";

export default function SearchCity() {
  const APP_KEY = "744b3384c5a94cad26fb50f00221e13c";
  const [query, setQuery] = useState("");
  const [search, setSearch] = useState("");

  const [weather, setWeather] = useContext(WeatherContext);

  useEffect(() => {
    fetchWeather();
  }, [query]);

  const updateSearch = (e) => {
    setSearch(e.target.value);
  };

  const updateWeather = (e) => {
    e.preventDefault();
    setQuery(search);
    setSearch("");
  };

  window.addEventListener("load", () => {
    let long;
    let lat;

    navigator.geolocation
      ? navigator.geolocation.getCurrentPosition((position) => {
          long = position.coords.longitude;
          lat = position.coords.latitude;
          fetch(
            `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${long}&appid=${APP_KEY}`
          )
            .then((res) => res.json())
            .then((data) =>
              setWeather({
                weather: data.weather[0].description,
                icon: data.weather[0].icon,
                temp: Math.floor(data.main.temp - 273.15),
                wind: Math.ceil((data.wind.speed / 1000) * 3600),
                deg: data.wind.deg,
                country: data.sys.country,
                name: data.name,
                clouds: data.clouds.all,
              })
            );
        })
      : alert("Please enter a city");
  });

  const fetchWeather = (props) => {
    let checkFetch = (response) => {
      if (!response.ok) {
        throw alert("Please enter an existing City name");
      }
      return response;
    };
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${[
        query,
      ]}&appid=${APP_KEY}`
    )
      .then(checkFetch)
      .then((res) => res.json())
      .then((data) => {
        setWeather({
          icon: data.weather[0].icon,
          weather: data.weather[0].description,
          temp: Math.floor(data.main.temp - 273.15),
          wind: Math.ceil((data.wind.speed / 1000) * 3600),
          country: data.sys.country,
          deg: data.wind.deg,
          name: data.name,
          clouds: data.clouds.all,
        });
      });
  };

  return (
    <div>
      <form className="form" onSubmit={updateWeather}>
        <div className="input-container">
          <input
            type="text"
            autoFocus
            onChange={updateSearch}
            value={search}
            placeholder="Please enter a City name"
          />
          <img className="input-image" src={searchIcon} alt="search icon" />
        </div>

        <br />
        <button
          type="submit"
          value="submit"
          className="search-btn"
          onClick={updateWeather}
        >
          {" "}
          Submit{" "}
        </button>
      </form>
    </div>
  );
}
